import React from "react";
var currentTheme = 1;
var root = document.querySelector(':root');


function changeTheme() {
    switch (currentTheme) {
        case 1:
            //dark
            root.style.setProperty('--navcolor', '#323235');
            root.style.setProperty('--maincolor', '#282828');
            root.style.setProperty('--secondmaincolor', '#404040');
            root.style.setProperty('--navtextcolor', 'white');
            root.style.setProperty('--titlecolor', 'white');
            root.style.setProperty('--text', 'white');
            root.style.setProperty('--invertext', 'black');
            root.style.setProperty('--color1', '#399E5A');
            root.style.setProperty('--color2', '#5ABCB9');
            root.style.setProperty('--color3', '#644CAD');
            break;
        case 2:
            //gruv
            root.style.setProperty('--navcolor', '#3c3836');
            root.style.setProperty('--maincolor', '#282828');
            root.style.setProperty('--secondmaincolor', '#504945');
            root.style.setProperty('--navtextcolor', '#ebdbb2');
            root.style.setProperty('--titlecolor', '#ebdbb2');
            root.style.setProperty('--text', '#ebdbb2');
            root.style.setProperty('--invertext', '#282828');
            root.style.setProperty('--color1', '#cc241d');
            root.style.setProperty('--color2', '#d79921');
            root.style.setProperty('--color3', '#98971a');
            break;
        case 3:
            //light
            root.style.setProperty('--navcolor', 'white');
            root.style.setProperty('--maincolor', '#d7daea');
            root.style.setProperty('--secondmaincolor', '#e6e7ef');
            root.style.setProperty('--navtextcolor', 'grey');
            root.style.setProperty('--titlecolor', 'black');
            root.style.setProperty('--text', 'black');
            root.style.setProperty('--invertext', 'white');
            root.style.setProperty('--color1', '#F75C03');
            root.style.setProperty('--color2', '#FFC857');
            root.style.setProperty('--color3', '#0E79B2');
            break;
    }
}


if (localStorage.getItem("stTheme") != null) {
    currentTheme = parseInt(localStorage.getItem("stTheme"));
    changeTheme();
}

function iterate() {
    if (currentTheme < 3) {
        currentTheme++;
    }
    else {
        currentTheme = 1;
    }
    changeTheme();
    localStorage.setItem("stTheme", currentTheme + "");
}


function Navbar() {
  return (
			<nav className="navbar">
				<div className="leftnav">
					<a className="name"><span className="YuMe">Yurii M's webpage</span><span className="YM">YM</span></a>
				</div>
				<div className="rightnav">
					<ul>
						<li><span>home</span><i className="fas fa-home"></i></li>
						<li onClick={iterate} id="themebutton"><span>theme</span><i className="fas fa-paint-brush"></i></li>
					</ul>
				</div>
			</nav>
  );
}


export default Navbar;
